import React from 'react';
import { createCommand } from '@commander-js/extra-typings';
import type { CommandServices } from '@wix/cli-command-contract';
import { createI18n } from '../i18n.js';

export const login = (services: CommandServices) => {
  const i18n = createI18n();

  return createCommand('login')
    .description(i18n.t('login_command.description'))
    .option('--api-key <token>', i18n.t('login_command.option.api_key'))
    .action(async (options, command) => {
      const { apiKey } = options;
      const { getUserInfo } = await import('@wix/cli-auth');
      const { LoginCommand } = await import(
        '../components/LoginCommand/index.js'
      );
      const { renderCommand } = await import('./render-command.js');

      const userInfo = await getUserInfo();

      await renderCommand(
        command,
        services,
        <LoginCommand apiKeyToken={apiKey} userInfo={userInfo} />
      );
    });
};
