import { cwd } from 'node:process';
import { createRequire } from 'node:module';
import { join } from 'node:path';
import { pathToFileURL } from 'node:url';
import semverPrerelease from 'semver/functions/prerelease.js';
import { CliError, CliErrorCode } from '@wix/cli-error';
import { version as cliVersion } from '../package.json';

const requireCwd = createRequire(
  join(
    cwd(),
    // `createRequire` must accept a path to a file, but doesn't really care about it
    // Even Sindre did it:
    // https://github.com/sindresorhus/import-from/blob/6063941cfac93086ae7f5a2e9ec2ac23f4bee401/index.js#L9
    'dummy.js'
  )
);

export async function importCliApp(): Promise<
  typeof import('@wix/cli-command-contract') | null
> {
  try {
    const cliAppPath = requireCwd.resolve('@wix/cli-app');
    ensureCompatibleVersions();
    return await import(pathToFileURL(cliAppPath).href);
  } catch (e) {
    if (e instanceof Error && 'code' in e && e.code === 'MODULE_NOT_FOUND') {
      return null;
    }
    throw e;
  }
}

function ensureCompatibleVersions() {
  const cliAppVersion = requireCwd('@wix/cli-app/package.json').version;

  // Skip version check for prerelease versions (e.g. falcon deploy previews)
  if (semverPrerelease(cliAppVersion) !== null) {
    return;
  }

  if (cliVersion !== cliAppVersion) {
    throw new CliError({
      code: CliErrorCode.CliAppVersionMismatch({ cliVersion, cliAppVersion }),
    });
  }
}
