import React from 'react';
import { createCommand } from '@commander-js/extra-typings';
import type { CommandServices } from '@wix/cli-command-contract';
import { createI18n } from '../i18n.js';

export const whoami = (services: CommandServices) => {
  const i18n = createI18n();

  return createCommand('whoami')
    .description(i18n.t('whoami_command.description'))
    .action(async (_, command) => {
      const { getUserInfo } = await import('@wix/cli-auth');
      const { WhoamiCommand } = await import('../components/WhoamiCommand.js');
      const { renderCommand } = await import('./render-command.js');

      const userInfo = await getUserInfo();

      await renderCommand(
        command,
        services,
        <WhoamiCommand userInfo={userInfo} />
      );
    });
};
