import { createCommand, Option } from '@commander-js/extra-typings';
import type { CommandServices } from '@wix/cli-command-contract';
import { createI18nT } from '../i18n.js';

export const previewCommand = (
  services: CommandServices,
  cliVersion: string
) => {
  const t = createI18nT();

  return createCommand('preview')
    .description(t('site_preview_command_def.description'))
    .addOption(
      new Option(
        '--source <local|remote>',
        t('site_preview_command_def.option.source')
      ).choices(['local', 'remote'] as const)
    )
    .action(async (options, command) => {
      const { runCommand } = await import('./run-command.js');
      const { preview } = await import('../preview/index.js');

      await runCommand(command, services, cliVersion, () =>
        preview(services, options)
      );
    });
};
