import { cwd } from 'node:process';
import pTimeout from 'p-timeout';
import fetchPackageJson from 'package-json';
import type { ErrorReporter } from '@wix/cli-error-reporting';
import { CliError, CliErrorCode } from '@wix/cli-error';
import type { CacheState } from '@wix/cli-fs-cache';
import { createFsCache, getFsCacheFilePath } from '@wix/cli-fs-cache';
import { getTestOverrides } from '@wix/cli-test-overrides';
import { writeCrashReport } from '@wix/cli-debug-log';
import {
  VERSION_FILE_NAME,
  getDebugLogFilePath,
} from '@wix/cli-core-definitions';
import { join } from 'node:path';
import { z } from 'zod';
import { match } from 'variant';
import packageJson from '../package.json';

const versionCacheFileSchema = z.object({
  deprecated: z.string().optional(),
});

export type VersionCacheFile = z.TypeOf<typeof versionCacheFileSchema>;

export async function deprecationCheck(errorReporter: ErrorReporter) {
  if (process.env.NODE_ENV == 'development') {
    return;
  }

  let versionCache: VersionCacheFile | null = null;

  try {
    const { version: packageVersion, name: packageName } = packageJson;

    const cliCache = createFsCache({
      filePath: getFsCacheFilePath(join(__dirname, '..'), VERSION_FILE_NAME),
    });

    const cacheState = await cliCache.get();

    versionCache = matchVersionState(cacheState);

    if (!versionCache) {
      const cliPackageDetails = await fetchPackageDetails({
        packageName,
        packageVersion,
      });

      versionCache = {
        ...cliPackageDetails,
      };

      await cliCache.set(versionCache);
    }
  } catch (e) {
    const deprecationError = new CliError({
      code: CliErrorCode.FailedToCheckDeprecatedStatus(),
      cause: e,
    });
    errorReporter.reportError(deprecationError);
    writeCrashReport(getDebugLogFilePath(cwd()), deprecationError);
  }

  if (versionCache?.deprecated) {
    throw new CliError({
      code: CliErrorCode.DeprecatedVersion({
        currentVersion: packageJson.version,
      }),
    });
  }
}

function matchVersionState(cacheState: CacheState) {
  return match(cacheState, {
    Exists: ({ data: _versionCache }) => {
      const cache = versionCacheFileSchema.safeParse(_versionCache);
      return cache.success ? cache.data : null;
    },
    NotExists: () => null,
  });
}

async function fetchPackageDetails({
  packageName,
  packageVersion,
}: {
  packageName: string;
  packageVersion: string;
}) {
  const testOverrides = getTestOverrides();

  const packageJsonPromise = fetchPackageJson(packageName, {
    version: packageVersion,
    registryUrl: testOverrides.registryUrl,
  });

  const result = await pTimeout(packageJsonPromise, {
    milliseconds: 5000,
    message: 'Reached timeout while fetching Wix CLI package details',
  });

  if (result instanceof Error) {
    throw result;
  }

  return {
    deprecated: result.deprecated as string | undefined,
  };
}
